export class Email {
  name:string;
  emailTo:string;
  emailFrom:string;
  subject:string;
  message:string;

  constructor(name:string, emailTo:string, emailFrom:string, subject:string, message:string) {
    this.name = name;
    this.emailTo = emailTo;
    this.emailFrom = emailFrom;
    this.subject = subject;
    this.message = message;
  }
}



