import {Component, OnDestroy} from '@angular/core';
import {AppService} from "./app.service";
import {Email} from "./objects";
import {Subscription} from "rxjs/Subscription";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent implements OnDestroy {
  private emailSubscription:Subscription;

  constructor(public appService:AppService) {
    this.emailSubscription = this.appService.testEmail.subscribe(
      success => {
        console.log(success);
      }
    )
  }

  ngOnDestroy() {
    this.emailSubscription.unsubscribe();
  }

  send() {
    let email:Email = new Email("Christian", "christian.jonathan.coan@gmail.com", "blah@blah.org", "Hello", "Hello");
    this.appService.sendEmail(email);
  }
}



